@import './../../../../../styles/sass/abstracts/mixins.scss';
@import './../../../../../styles/sass/abstracts/variables.scss';


.box {
   width: 271px;
   height: 403px;
   border: 1px solid #CDCDCD;

   padding-top: 16px;
   padding-right: 16px;
   padding-left: 16px;

   position: relative;
}

.sale {
   background-color: $accentColor;

   color: $veryLightColor;

   width: 60px;
   height: 30px;

   display: flex;
   justify-content: center;
   align-items: center;

   position: absolute;
   top: 0;
   left: 0;
}

.like {
   display: flex;
   justify-content: end;
}

.wrap {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
}

.like {
   text-align: end;
}

.productImg {
   margin-bottom: 15px;
}

.title {
   @include text (18px, 400, 21px, inherit);

   width: 214px;
   text-align: center;

   margin-bottom: 7px;
}


.price {
   @include text (30px, 600, 36px, inherit);
   font-family: "BarlowBold";

   padding-top: 13px;
}


.nope {
   @include text (18px, 700, 21px, $basicTextColor);
   margin-bottom: 10px;
}

.inform {
   @include text (16px, 400, 27px, $accentColor);
   border-bottom: 1px solid $accentColor;
}

.cartLink {
   width: 60px;
   height: 40px;

   background-color: $accentColor;
   border-top-left-radius: 10px;

   display: flex;
   justify-content: center;
   align-items: center;

   position: absolute;
   bottom: 0;
   right: 0;
}

.box:hover {

   cursor: pointer;
   color: $accentColor;

   &::after {
      content: "Посмотреть товар";
      @include text (20px, 400, 21px, $basicTextColor);

      width: 230px;
      height: 61px;

      // background-color: $veryLightColor;

      // box-shadow: rgba(0, 0, 0, 0.2);

      background: #FFFFFF;
      opacity: 0.9;
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
      border-radius: 3px;

      display: flex;
      justify-content: center;
      align-items: center;

      position: absolute;
      top: 30%
   }

}